.navbar {
  background-color: #0b0c10 !important;
  padding: 1rem;
  z-index: 20;
}

.link {
  text-decoration: none;
  display: inline-block !important;
  color: #45a29e !important;
  font-weight: 700;
  font-family: 'Overpass', sans-serif;

}
.nav-item {
  margin-right: 10px;
  margin-left: auto;
  text-align: center;
}
.logo {
  text-decoration: none !important;
  font-size: 1.4rem;
  font-weight: 500;
  margin-left: 10px;
  color: #64fcf2;
  font-family: "Lato", sans-serif;
}

.logo:hover {
  color: #64fcf2;
}
.navbar-nav {
  margin-left: auto;
  display: flex;
}

.botonMenuDesplegable {
  margin-right: 10px;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(100, 252, 242, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.link:after {
  display: block;
  content: "";
  border-bottom: solid 1px #45a29e;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}

.link:hover:after {
  transform: scaleX(1);
}

@media (max-width: 767.98px) {
  .nav-item {
    margin: auto;
  }
}
