.fila {
  width: 50%;
  margin-left: auto !important;
  margin-right: auto !important;
}
.progreso {
  margin-top: 10px !important;
  margin-bottom: 10px !important;
}
.imagen-conocimiento {
  display: block;
  width: 50%;
  margin: auto;
}

.texto-progress {
  text-align: center;
  display: block;
  font-family: 'Overpass', sans-serif;
}

@media (max-width: 767.98px) {
  .texto-progress {
    font-size: 12px;
  }
  .progreso {
    margin-right: auto !important;
    margin-left: auto !important;
  }
  .fila {
    width: 90%;
  }
}
.titulo-conocimiento {
  font-weight: 900;
  color: #1f2833;
  font-family: "Lato", sans-serif;
}
